@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

$size-medium: 60px;
$size-small: 26px;

.app-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: $size-medium;
  height: $size-medium;
  border-radius: $size-medium / 4;
  overflow: hidden;

  img {
    opacity: 1;
    width: 100%;
    height: 100%;
    transition: opacity 200ms ease-in-out;
  }

  &--small {
    display: inline-flex;
    vertical-align: middle;
    width: $size-small;
    height: $size-small;
    border-radius: 5px;
  }

  &--loading,
  &--error {
    border: 1px dashed $btn-color-bg;

    img {
      opacity: 0;
    }
    .loading {
      position: absolute;
      margin: 0;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &--error {
    background-color: $font-color-light;

    &:after {
      content: '!';
      position: absolute;
      color: $font-color-beta-light;
      font-size: 30px;
    }
  }

  &.skeleton,
  &.skeleton.error {
    border: 0;

    .loading {
      display: none;
    }
    &:after {
      display: none;
    }
  }

  &.skeleton-style-page-bg {
    background-color: $color-border;
  }

  &.skeleton-style-tile {
    background-color: rgba($color-content, 0.08);
  }

  &.skeleton-style-list {
    background-color: rgba(white, 0.1);
  }
}
