@keyframes spinner {
  100% {
    transform: rotate(360deg);
  }
}

@mixin spinner {
  &:after {
    content: '';
    border: $loader-width solid $loader-color-bg;
    border-left-color: $loader-color-spinner;
    height: $loader-size;
    width: $loader-size;
    display: block;
    margin: 0 auto 20px;
    border-radius: $loader-size/2;
    animation: spinner $loader-speed linear infinite;
  }
}

%spinner {
  @include spinner;
}

@mixin spinner-abs {
  @include spinner

  &:after {
    position: absolute;
    top: $loader-position;
    left: $loader-position;
    margin: (-$loader-size/2) 0 0 (-$loader-size/2);
  }
}

%spinner-abs {
  @include spinner-abs;
}
