@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.indicator-nav-button {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  align-items: center;
  padding-right: 10px;

  &__indicator {
    text-transform: uppercase;
    color: $btn-color-bg;
    margin-left: 5px;

    &--active {
      color: $font-color-light;
    }
  }

  &__icon {
    display: inline-block;
    vertical-align: top;
    color: $color-warning;
  }
}
