@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.upload-file {
  &:focus {
    outline: none;
  }
}

.upload-file__button {
  width: 100%;
}

.upload-file__button--drop-active {
  color: $font-color-light;
  background-color: $btn-color-main;
}

.upload-file__selected-files-list {
  overflow: hidden;

  .closing-tag {
    margin-left: 10px;
  }

  .closing-tag:first-child {
    margin-left: 0;
  }
}

.upload-file__selected-files-upload-button {
  color: $font-color-beta-light;
  font-size: 15px;
  cursor: pointer;

  &:hover {
    color: $btn-color-hover;
  }
}
