@import '~@adjust/adjust-style/scss/_variables';
@import '~@adjust/adjust-style/scss/themes/adjust-dark/_variables';
@import '~@/assets/styles/scss/_variables.beta';

$overlay-bg-color: rgba(216, 222, 229, .3);
$modal-size: 700px;
$modal-small-size: 360px;
$modal-medium-size: 500px;

.react-modal__overlay {
  position: absolute;
  z-index: 200;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: opacity 300ms $deceleration-curve-timing;
  opacity: 0;
  background: $overlay-bg-color;

  &.ReactModal__Overlay--after-open {
    opacity: 1;
  }
}

.react-modal__content {
  position: relative;
  display: flex;
  overflow: auto;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
  width: $modal-size;
  padding: 20px;
  opacity: 1;
  border: 1px solid $color-border;
  outline: none;
  background: #fff;
  -webkit-overflow-scrolling: touch;

  &--small {
    width: $modal-small-size;
    height: $modal-small-size;
  }

  &--medium {
    width: $modal-medium-size;
    height: $modal-medium-size;
  }
}

.react-modal__actions {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: auto;
}
