@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.nav-button {
  position: relative;

  &__button {
    position: relative;
    background: none;
    display: block;
    width: 100%;
    text-align: left;
    text-transform: none;
    padding: 11px 20px;
    border-radius: 0;
    letter-spacing: initial;
    font-size: 13px;

    &:focus {
      background-color: transparent;
    }

    &:hover {
      background-color: $btn-color-disabled;

      .nav-button__icon {
        color: $font-color-light;
      }
    }

    &--disabled {
      color: $btn-color-bg;
      cursor: not-allowed;

      &:focus {
        color: $btn-color-bg;
      }

      &:hover {
        color: $btn-color-bg;
        background-color: transparent;

        .nav-button__icon {
          color: $btn-color-bg;
        }
      }
    }
  }

  &__inner-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &__content {
    flex: 1 1 auto;
  }

  &__icon {
    display: flex;
    color: $btn-color-bg;
    font-size: 11px;
  }

  &__tooltip {
    top: 90%;
    left: 50%;
    right: auto;
    transform: translate(-50%, 0);
    width: max-content;
    text-align: center;
    margin-top: 0;
  }
}
