.react-modal__content--alert {
  text-transform: uppercase;
  flex-direction: row;
  padding: 0 20px;
  justify-content: center;
  align-items: center;
  min-height: 52px;

  .react-modal__content__icon {
    width: 45px;
    display: block;
    margin-right: 10px;

    i {
      font-size: 28px;
    }
  }

  .react-modal__content__message {
    padding: 18px 0;
    margin: 0;
    line-height: 20px;
    width: 100%;
  }

  .react-modal__actions {
    width: 40px;
    margin-left: auto;
    margin-top: 0;
    padding: 0;
    border-top: none;
  }
}
