@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.iconic-radio-group-field {
  .form-row.error > & > .iconic-tab {
    display: inline-block;

    i, span {
      color: $font-warning;
    }

    input[type=checkbox],
    input[type=radio] {
      &:disabled + i, &:disabled + i + span,
      &:focus + i, &:focus + i + span {
        color: $font-warning;
      }
    }
  }
}
