@import '~@adjust/adjust-style/scss/_variables';
@import '~@/assets/styles/scss/_variables.beta';

.react-modal__overlay--sidebar-modal {
  background-color: rgba($sidebar-color, 0.8);

  &.ReactModal__Overlay--before-close {
    animation: 300ms zoomOut $deceleration-curve-timing both;
  }
}

.react-modal__content--sidebar-modal {
  width: 100%;
  height: auto;
  background-color: $sidebar-highlight-color;
  color: #fff;
  border: none;
  padding-bottom: 0;
  font-size: .9em;

  &.ReactModal__Content--after-open {
    animation: 300ms slideInRight $deceleration-curve-timing;
  }

  .react-modal__actions {
    justify-content: center;
    border-top: 2px solid $sidebar-hover-color;
    height: 55px;
    margin-left: -20px;
    padding: 0 20px;
    width: calc(100% + 40px);

    .react-modal__actions__btn {
      background: transparent;
      opacity: .5;
      font-size: 13px;
      transition: opacity .3s ease-in-out;

      &:hover,
      &:focus {
        opacity: 1;
      }

      &:disabled {
        opacity: .2;
      }

      &:focus {
        outline: 1px solid $sidebar-hover-color;
      }
    }
  }
}

.react-modal__content__title {
  color: #fff;
  font-size: 1em;
  margin-bottom: 20px;
}

.react-modal__content__message {
  margin-top: 0;
  margin-bottom: 20px;
}

.react-modal__content--empty {
  padding-top: 0;

  .react-modal__actions {
    border-top: none;
  }
}

.react-modal__content--info {
  background-color: $color-main;
}

.react-modal__content--error {
  background-color: $btn-color-destroy;
}
