@import "~@adjust/adjust-style/scss/_variables";
@import "~@/assets/styles/scss/_variables.beta";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";

.additional-package {
  background-color: $sidebar-color;
  padding: 30px 30px 100px 30px;
  border-bottom: 2px solid #95A1B7;
  min-height: 175px;
  position: relative;
}

.additional-package__name,
.additional-package__description {
  margin: 0;
}

.additional-package__name {
  color: $font-color-grey;
  font-size: 1.30em;
  font-weight: 300;
  margin-bottom: 15px;
}

.additional-package__description {
  color: $btn-color-hover;
  font-size: 0.9em;
  line-height: 1.5em;
}

.additional-package--enabled {
  background-color: $btn-color-bg;
  border-bottom-color: $btn-color-bg;

  .additional-package__name,
  .additional-package__description {
    color: white;
  }
}

.additional-package__package-link {
  text-align: center;
  position: absolute;
  bottom: 30px;
  left: 0;
  width: 100%;
}

.additional-package__btn {
  font-size: 1em;
  padding: 8px 14px;
  display: inline-block;
  vertical-align: middle;
  text-transform: uppercase;
}

.additional-package__btn-placeholder {
  @extend .additional-package__btn;

  color: white;
  text-transform: uppercase;
}

