@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.fade-tooltip {
  word-break: keep-all;
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: 4px;
  padding: 4px 10px;
  border-radius: 6px;
  background-color: black;
  color: $font-color-light;
  font-size: 14px;
  z-index: 100;
  transition: opacity ease-in-out;
}

.fade-transition {
  &-enter {
    opacity: 0;
  }

  &-enter-active {
    opacity: 1;
  }

  &-exit {
    opacity: 1;
  }

  &-exit-active {
    opacity: 0;
  }
}
