@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.switcher-field {
  display: flex;
  align-items: center;

  &--new {
    flex-direction: row-reverse;
    justify-content: flex-end;

    .switcher.active:not(.disabled) {
      background-color: $btn-color-main;
    }

    .switcher.disabled {
      opacity: 0.45;
    }

    .label-block {
      margin-right: 10px;
      color: $switcher-color-text-on;
    }

    .disabled + .label-block {
      color: $font-color-beta-light;
    }
  }
}
