@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.closing-tag {
  white-space: nowrap;
}

.closing-tag__value {
  color: $font-color-light;
  font-size: 13px;
}

.closing-tag__close-button {
  cursor: pointer;
  color: $color-text;
  text-align: center;
  font-size: 8px;
  margin-left: 10px;

  &:hover {
    color: $btn-color-hover;
  }
}
