@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.guide-tooltip {
  padding: 15px;
  padding-right: 30px;
  min-width: 180px;
  max-width: 250px;
  position: relative;
  border-radius: 2px;
  margin: 5px;
  background-color: $guide-bg-color;
  color: $guide-font-color;
  font-size: 13px;
  line-height: 1.4em;

  &__close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px;
    font-size: 10px;
    background-color: transparent;
    color: $guide-font-color-icon;
    transition: color 200ms ease-in-out;

    &:hover {
      color: $guide-font-color;
      background-color: transparent;
    }
  }

  &__primary-btn {
    padding: 6px 11px;
    color: $guide-font-color;
    background-color: $guide-bg-color-btn;

    &:hover, &:focus {
      color: $guide-font-color;
      background-color: $guide-bg-color-btn-accent;
    }
  }

  &__footer {
    margin-top: 10px;
  }

  & + .__floater__arrow {
    span {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin: -20px !important;
      background-color: rgba(255,255,255,.3);

      &::after {
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: $btn-color-default;
      }
    }

    svg {
      display: none;
    }
  }
}
