@import "~@adjust/adjust-style/scss/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.tab {
  &__list {
    flex-shrink: 0;
    border-bottom: 2px solid $sidebar-hover-color;
  }

  &__item {
    margin: 0;
    border-radius: 0;
    text-transform: none;
    background-color: $sidebar-color;
    padding: 15px 20px;
    position: relative;
    transition: all 300ms ease-in-out;
    font-family: $font-family-maven;
    font-size: 16px;
    color: $sidebar-light-color;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: 2px;
      transition: background-color 300ms ease-in-out;
    }

    &:hover, &:focus, &--active {
      color: #fff;
      background-color: $sidebar-hover-color;

      &::after {
        background-color: $btn-color-main;
      }
    }
  }
}
