@import "~@adjust/adjust-style/scss/_variables";
@import "~@adjust/adjust-style/scss/themes/adjust-dark/_variables";
@import "~@/assets/styles/scss/_variables.beta";

.search-field {
  position: relative;
  display: flex;
  align-items: center;
  background: none;
  width: 100%;
  border: none;
  padding-left: 10px;

  &__icon {
    position: absolute;
    left: 0;
    margin-left: 5px;

    .icon-search {
      margin-left: 4px;
    }
  }

  &__icon .icon-search,
  &__reset .icon-times {
    display: block;
    line-height: 25px;
    color: $btn-color-bg;

    &:focus,
    &:hover {
      color: $font-color-light;
    }

    input:active ~ &,
    input:focus ~ &,
    input:hover ~ & {
      color: $font-color-light;
    }

    .form-row-dark input:hover ~ & {
      color: $font-color-dark;
    }

    .form-row-dark input:disabled:hover ~ &,
    .form-row-dark input:active ~ &,
    .form-row-dark input:focus ~ &,
    input:disabled:hover ~ & {
      color: $btn-color-bg;
    }

    .form-row-dark & {
      color: $btn-color-bg;

      &:focus,
      &:hover {
        color: $btn-color-bg;
      }
    }
  }

  input,
  input:active,
  input:focus,
  input:disabled:hover,
  input:disabled:focus,
  input:disabled:active
  {
    padding: 0 25px;
    margin: 0;
  }

  &__reset {
    position: absolute;
    right: 0;
    margin-right: 10px;

    .clear {
      font-size: 10px;
      background: transparent;
      margin: 0;
      padding: 0;

      &:disabled {
        background: transparent;
      }
    }
  }
}

.borderless input[type=text] {
  &,
  &:active,
  &:focus,
  &:hover {
    border-bottom: none;
    border: none;
  }
}
