@import '~@adjust/adjust-style/scss/_variables';
@import '~@/assets/styles/scss/_variables.beta';

.react-modal__content--confirm {
  padding-top: 0;

  .react-modal__description {
    padding-top: 20px;
    width: 100%;
  }

  .react-modal__content__subtitle {
    text-transform: uppercase;
  }

  .react-modal__content__body {
    
    a {
      color: white;
      text-decoration: underline;
  
      &:hover {
        text-decoration: none;
      }
    }
  }

  .react-modal__actions {
    justify-content: space-between;

    &-block:only-child {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }

  &.react-modal__content--info,
  &.react-modal__content--error {
    background-color: $sidebar-highlight-color;

    .react-modal__actions {
      border-top: none;
    }
  }

  &.react-modal__content--info {
    .react-modal__actions {
      background-color: $color-main;
    }
  }

  &.react-modal__content--error {
    .react-modal__actions {
      background-color: $btn-color-destroy;
    }
  }

  .react-modal__actions-block {
    flex-shrink: 0;
  }
}
